import React, {useState, useEffect, useRef} from 'react';
import { NavLink } from 'react-router-dom';
import Slider from "react-slick";
import shoeWLegs from '../../assets/img/shoes/shoe-w-legs.png';
import shoe from '../../assets/img/shoes/shoe.png';
import ReactStars from "react-rating-stars-component";
import Voucher from '../Voucher';
import ProductQuality from '../ProductQuality';
import SizeChart from '../productDetails/SizeChart';
import LocationDropdown from './LocationDropdown';
import ExpeditionDropdown from './ExpeditionDropdown';
import Label from '../Label';
import dropdownIcon from '../../assets/category-icons/dropdown-icon.svg';

const firstExample = {
  size: 15,
  value: 4,
  edit: false,
  color: '#EAEAEA',
  activeColor: '#FFD800'
};

const settingsSliderTop = {
  dots: false,
  infinite: true,
  speed: 500,
  draggable: false,
  centerPadding: "0",
  fade: true,
}

const settingsSliderBottom = {
  dots: false,
  infinite: true,
  speed: 500,
  slidesToShow: 3,
  slidesToScroll: 1,
  swipeToSlide: true,
  centerPadding: "0",
}

const ProductDetails = () => {
  const [nav1, setNav1] = useState();
  const [nav2, setNav2] = useState();
  const sliderTop = useRef(null);
  const sliderBottom = useRef(null);

  useEffect(() => {
    setNav1(sliderTop.current);
    setNav2(sliderBottom.current);
  }, []);

  return (
    <div className='product-details'>
      <NavLink to='/' className='back-to-before'><span><img src={dropdownIcon} alt="dropdown icon" /></span>Kembali ke Sepatu Pria</NavLink>
      <div className="details">
        <div className="details-photos">
          {/* <div className="highlight-photo">
            <img src={shoeWLegs} alt="sepatu kaki" />
          </div> */}
          <Slider {...settingsSliderTop} 
            asNavFor={nav2} 
            ref={sliderTop} > 
            <img src={shoeWLegs} alt="sepatu kaki" />
            <img src={shoe} alt="sepatu kaki" />
            <img src={shoe} alt="sepatu kaki" />
            <img src={shoe} alt="sepatu kaki" />
          </Slider>
          <div className="slider-highlight">
            <Slider {...settingsSliderBottom}
              asNavFor={nav1}
              ref={sliderBottom}
              slidesToShow={3}
              swipeToSlide={true}
              focusOnSelect={true}>
              <div className="slider-photo">
                <img src={shoeWLegs} alt="sepatu kaki" />
              </div>
              <div className="slider-photo">
                <img src={shoe} alt="sepatu kaki" />
              </div>
              <div className="slider-photo">
                <img src={shoe} alt="sepatu kaki" />
              </div>
              <div className="slider-photo">
                <img src={shoe} alt="sepatu kaki" />
              </div>
            </Slider>
          </div>
        </div>

        <div className="details-description">
          <div className="details-description-category">Sepatu Pria</div>
          <div className="details-description-title">Sepatu Yeezy 350 v2 Black Red</div>

          <div className="details-description-information">
            <ReactStars {...firstExample} />
            <p className="feedbacks-count"><span>254</span> Ulasan</p>
            <p className="discussion-count"><span>12</span> Diskusi Produk</p>
            <p className="sold-count"><span>8</span> Terjual</p>
          </div>

          <div className="details-description-old-price">
            <p className="discount-price">Rp. 8.500.000</p>
            <Label type='discount' content='50% OFF' />
          </div>

          <p className="details-description-actual-price">Rp. 4.250.000</p>

          <div className="details-description-store-promo">
            <h5 className="information-title">Promo Toko</h5>
            <div className="voucher-wrapper">
              <Voucher title='Gratis Ongkir' preview='Area Jabodetabek' />
              <Voucher title='Cashback 30rb' preview='Min. Transaksi 100rb' />
            </div>
          </div>

          <div className="details-description-product-quality">
            <h5 className="information-title">Kualitas Barang</h5>
            <div className="quality-wrapper">
              <ProductQuality quality={'Original'} />
              <ProductQuality quality={'Baru'} />
            </div>
          </div>

          <div className="details-description-sizes">
            <h5 className="information-title">Ukuran <span>Rician Ukuran</span></h5>
            <SizeChart sizes={[38, 40, 42, 43, 46, 44, 47, 49]} />
          </div>

          <div className="details-description-shipping">
            <h5 className="information-title">Estimasi Pengiriman</h5>
            <p className="shipping-promo">Gratis Ongkir min. pembelian Rp. 2.500.000</p>
            <div className="shipping-counter">
              <LocationDropdown />
              <ExpeditionDropdown />
            </div>
          </div>

        </div>
      </div>
    </div>
  )
}

export default ProductDetails
