import React, { useEffect, useState } from 'react';
import Size from './Size';

const SizeChart = ({ sizes }) => {
  const [newSizes, setNewSizes] = useState([]);
  const [sortedSizes, setSortedSizes] = useState(sizes)

  useEffect(() => {
    setSortedSizes([...sizes.sort()]);

    for (let i = sortedSizes[0]; i <= sortedSizes[sortedSizes.length - 1]; i++) {
      if (sortedSizes.includes(i)) {
        setNewSizes(prev => [...prev, i])
      } else {
        setNewSizes(prev => [...prev, false])
      }
    }

  }, []);

  return (
    <div className='size-chart'>
      {newSizes.map((size, i) => (
        <Size size={size} key={i} />
      ))}
    </div>
  )
}

export default SizeChart
