import React, { useContext } from 'react';
import { AppContext } from './../context/AppContext';
import shoe from '../../assets/img/shoes/shoe.png';
import ReactStars from "react-rating-stars-component";

const firstExample = {
  size: 13,
  value: 4,
  edit: false,
  color: '#EAEAEA',
  activeColor: '#FFD800'
};

const DetailRecommendation = () => {
  const { RecommendationProduct } = useContext(AppContext);

  return (
    <div className='recommend-produk'>

      <div className="recommend-produk-header">
        <p className="header-text-left">Rekomendasi Sepatu Pria Untuk Anda</p>
        <p className="header-text-right">Lihat Semua</p>
      </div>

      {RecommendationProduct.map((res, i) => (

        <div className="recommend-produk-card" key={i}>
          <img src={shoe} className="recommend-produk-img" alt='sepatu' />
          <div className="information-produk">
            <p className="title-produk">{res.productName}</p>

            <div className="flexing-text">
              <div className="star">
                <ReactStars {...firstExample} />
                <div className="terjual">
                  <p className="terjual-blue">{res.selling}</p>
                  <p className="terjual-grey"> terjual</p>
                </div>
              </div>

              <div className="price">
                <p className="cross-line">{res.price}</p>
                <p className="red-text">{res.discountPrice}</p>
              </div>

              <div className="box-text">
                <p className="red-box">{res.discount}</p>
                <p className="grey-box">Gratis Ongkir</p>
              </div>
            </div>
          </div>
        </div>
      ))}
    </div>
  )
}

export default DetailRecommendation
